import Vue from 'vue'
import Router from 'vue-router'
import About from '@/components/About'
import Works from '@/components/Works'
import Milestones from '@/components/Milestones'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: About
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/works',
      name: 'Works',
      component: Works
    },
    {
      path: '/milestones',
      name: 'Milestones',
      component: Milestones
    }
  ]
})



